@charset "utf-8";
@use "sass:math";
@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

@import './sub', './about';

* {margin: 0; padding: 0;}
li {list-style: none;}
a {text-decoration: none; color: inherit;}


$color: orange;

// 색상 내장 함수

@mixin center() {
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  h1 {
    color: $color;
  }
}